import * as React from 'react';
import * as audioActions from '../actions/audio';
import { MixFile } from "../types/index";

export const timeUpdate = (action: typeof audioActions.setCurrentTime) =>
    (event: React.SyntheticEvent<HTMLAudioElement>) => {
        const { currentTime } = event.currentTarget;
        (async () => {
            await action(currentTime);
        })();
    };

export const getAudioSources = (files: MixFile[], mixId: string) =>
    files
        .filter(f => [/ogg/i, /mp3/i].some(r => r.test(f.format)))
        .map((f, i) => (
            <source
                key={`audioSource-${i}`}
                src={`https://archive.org/download/${mixId}/${f.name}`}
            />
        ));
