import * as React from 'react';
import { connect } from 'react-redux';
import { css } from 'aphrodite';
import { MixFile, State } from "../../types/index";
import * as actions from '../../actions/audio';
import { getCurrentMixFiles } from "../../selectors/audio";
import { timeUpdate, getAudioSources } from "../../util/player";
import { styles } from "./styles";

const UUID = require('uuid-js');

interface Props {
    currentMixId: string;
    elementId: string;
    files: MixFile[];
}

const mapState = (state: State) => ({
    currentMixId: state.audio.currentMixId,
    elementId: state.audio.elementId,
    files: getCurrentMixFiles(state)
});

type PlayerType = React.SFC<Props & typeof actions>;
export const Player: PlayerType = props => (
    <audio
        id={props.elementId}
        controls // TODO: do own controls
        autoPlay // TODO: user option for autoplay (cookie, too)
        // muted // TODO: FIXME: turn off later
        className={css(styles.player)}
        onTimeUpdate={timeUpdate(props.setCurrentTime)}
        onLoadStart={e => {
            if (props.elementId === 'audioElement') {
                props.setElementId(
                    `audio-${UUID.create().toString()}`
                );
            }
        }}
    >
        {getAudioSources(props.files, props.currentMixId)}
    </audio>
);

export default connect(mapState, { ...actions })(Player);
