import { Audio } from '../types';
import * as actions from '../actions/audio';
import cue from './audio.temp.cueText';

const initial: Audio = {
    currentMixId: '',
    mixes: [],
    elementId: 'audioElement',
    currentTrack: 0,
    currentTime: 0,
    playing: false
};

export const audio = (state = initial, action: actions.AudioAction): Audio => {
    switch (action.type) {
        case 'AUDIO_SET_ELEMENT_ID':
            return {
                ...state,
                elementId: action.elementId
            };
        case 'AUDIO_SET_CURRENT_MIX_ID':
            return {
                ...state,
                currentMixId: action.currentMixId
            };
        case 'AUDIO_SET_CURRENT_TRACK_DONE':
            return {
                ...state,
                currentTrack: action.currentTrack
            };
        case 'AUDIO_SET_CURRENT_TIME_DONE':
            return {
                ...state,
                currentTime: action.currentTime
            };
        case 'AUDIO_SET_PLAYING_DONE':
            return {
                ...state,
                playing: action.playing
            };
        case 'AUDIO_MIXES_FETCHING':
            return state; // TODO: fetching flag
        case 'AUDIO_MIXES_FETCHED':
            return {
                ...state,
                mixes: action.mixes
            };
        case 'AUDIO_MIX_FETCHING':
            return state; // TODO: fetching flag
        case 'AUDIO_MIX_FETCHED': {
            const { mix } = action;
            return {
                ...state,
                mixes: state.mixes.map(m => m.id === mix.id ? mix : m)
            };
        }
        default:
            return state;
    }
}
