import * as React from 'react';
import { connect } from 'react-redux';
import { State, CueSheet } from '../../types';
import * as audioActions from '../../actions/audio';
import { getCurrentMix } from '../../selectors/audio';

export type TracklistState = {
    cueSheet: CueSheet;
} & typeof audioActions;

export type Type = React.SFC<TracklistState>;

export const Tracklist: Type = ({ cueSheet, setCurrentTrack }) => {
    return (
        <div>
            {cueSheet.tracks && cueSheet.tracks.map((track, i) => {
                return (
                    <div key={i}>
                        <button
                            onClick={() =>
                                setCurrentTrack(track.number - 1)}
                        >
                            {track.number} [{track.timeDisplay}]: {track.title}
                        </button>
                    </div>
                );
            })}
        </div>
    );
};

export const mapState = (state: State) => ({
    cueSheet: getCurrentMix(state).cueSheet
});

export const mapDispatch = {
    ...audioActions
};

export default connect(mapState, mapDispatch)(Tracklist);
