import { State, Track, Mix, CueSheet, MixFile } from '../types';
import { createSelector } from 'reselect';
import { parseCueSheet } from '../util/parseCueSheet'

export const getState = (state: State) => state.audio;
export const getCurrentTime = (state: State) => state.audio.currentTime;

export const getCurrentMix = createSelector(
    getState,
    audio => {
        const { currentMixId } = audio;
        const mix = audio.mixes.find(m => m.id === currentMixId);
        const fake = {
            files: [] as MixFile[],
            cueSheet: {
                tracks: [] as Track[]
            } as CueSheet
        };
        return mix || fake as Mix;
    }
);

export const getCurrentMixFiles = createSelector(
    getCurrentMix,
    currentMix => currentMix.files
);

export const getCurrentMixTracks = createSelector(
    getCurrentMix,
    currentMix => currentMix.cueSheet.tracks
);

export const getCurrentTrack = createSelector(
    [getCurrentMixTracks, getCurrentTime],
    (tracks, currentTime) =>
        tracks.reduce((prev, cur) =>
            (currentTime >= prev.time && currentTime < cur.time)
                ? prev
                : cur
        , { time: -99999999999, title: '' })
);

export const getMixes = createSelector(
    getState,
    audio => audio.mixes
        .slice()
        .map(m => ({ m, d: Date.parse(m.date) }))
        .sort((a, b) => (a.d < b.d)
            ? 1
            : (a.d > b.d)
                ? -1
                : 0)
        .map(o => o.m)
);
