import * as React from 'react';
import { connect } from 'react-redux';
import { State, Track, Mix } from '../../types';
import * as audioActions from '../../actions/audio';
import { getCurrentMix, getCurrentTrack } from '../../selectors/audio';
import Tracklist from '../Tracklist';
import Player from "../Player/index";
import Mixes from "../Mixes/index";

export type Props = {
    elementId: string;
    track: Track;
    mixes: Mix[];
    currentMixId: string;
    currentMix: Mix;
};

export type AudioViewType = React.SFC<Props>;

export const AudioView: AudioViewType = props => {
    const {
        elementId,
        track,
        currentMixId,
        currentMix
    } = props;

    return (
        <div>
            <h1>{currentMix.title}</h1>
            <Mixes />
            <Player />
            <div className="peaks">
                {currentMix && currentMix.files
                    .filter(f => /png/i.test(f.format))
                    .map(f => (
                        <img id={`peaks-${elementId}`} key={f.name}
                            src={`https://archive.org/download/${currentMixId}/${f.name}`}
                        />
                    ))
                }
            </div>
            <p>{track.number}{' '}{track.title}</p>
            <Tracklist />
        </div>
    );
};

export const mapState = (state: State) => ({
    elementId: state.audio.elementId,
    track: getCurrentTrack(state),
    currentMixId: state.audio.currentMixId,
    currentMix: getCurrentMix(state)
});

export default connect(mapState)(AudioView);
